.popup_wrapper{
    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    
    z-index: 100;
    
    background-color: rgba(0, 0, 0, 0.4);

    transition: all .3;
}

.popup{
    cursor: auto;
    position: relative;
    z-index: 112;

    max-width: 90%;
    max-height: 90%;
    overflow-y: scroll;

    box-shadow: 0 0 5px var(--border-color);
    border-radius: 20px;

    &::-webkit-scrollbar{
        display: none;
    }

    .btn_close{
        position: absolute;
        width: 50px;
        height: 50px;
        background-color: transparent;

        z-index: 105;

        top: 5px;
        right: 5px;
        transition: transform .2s ease;

        &::before,
        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;

            width: 50%;
            height: 3px;
            border-radius: 3px;

            background-color: var(--text-color);
            transform: translate(-50%, -50%) rotate(45deg);
        }

        &::after{
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}