.section{
    position: relative;

    margin: 0 auto;
    margin-top: 250px;

    max-width: 100%;
    width: 800px;
    aspect-ratio: 2/1;

    border-radius: 30px;
    background-color: #591393;
    background-image: url('../../../public/img/githubbg.jpg');
    background-size: cover;

    z-index: 2;
    box-shadow: var(--section-box-shadow);
}

@keyframes rotate{
    0% {transform: translate(-50%, -50%) rotate(0deg);}
    100% {transform: translate(-50%, -50%) rotate(360deg);}
}

.info{
    z-index: inherit;
    display: flex;
    color: #f1f1f1;

    width: 100%;
    height: 100%;
    
    font-size: 1.5em;
    text-align: center;
    gap: 15px;

    & span:first-child{
        position: absolute;
        left: 25px;
    }

    & span:last-child{
        position: absolute;
        right: 50px;
    }
    
    span{
        position: absolute;
        top: 50%;
        
        font-weight: 600;
        text-shadow: 0 0 5px rgb(225, 0, 255);
        transform: translateY(-50%);
    }
}

@media (max-width: 948px){
    .section{
        margin-top: 120px;
    }
}

@media (max-width: 630px) {
    .section{
        margin-top: 120px;
    }
    .info{
        font-size: 1.2em;

        & span:first-child{
            left: 20px;
        }
        & span:last-child{
            right: 35px;
        }
    }
}

@media (max-width: 505px) {
    .info{
        font-size: 1em;

        & span:first-child{
            left: 10px;
        }
        & span:last-child{
            right: 20px;
        }
    }
}