:root{
    --flag-w: 30px;
}

.lang_selector{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.lang_selector_img{
    filter: var(--link-filter);
    height: 30px;
}

.dropdown_wrapper{
    position: absolute;
    bottom: -110px;
    padding-top: 110px;
    width: fit-content;
    z-index: 19;
}

.dropdown{
    display: none;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: calc(var(--flag-w) / 2);
    
    bottom: 0;
    padding: calc(var(--flag-w) / 1.5) calc(var(--flag-w) / 2)  calc(var(--flag-w) / 2) calc(var(--flag-w) / 2);
    border-radius: 10px;
    
    background-color: rgb(0, 0, 0);    
    
    opacity: 0;
    
    transition: visibility 0.3s linear, opacity 0.3s linear;
    
    &.visible{
        opacity: 1;
        display: flex;
        
        animation: show .3s ease forwards;
    }
    
    .component{
        transition: transform .2s ease;
        
        img {width: var(--flag-w); border-radius: 3px;}
        
        &:hover{
            transform: scale(1.17);
        }
    }
}


@keyframes show {
    0%{display: flex; opacity: 0;}
    100%{opacity: 1;}
}

@media (max-width: 948px) {
    .lang_selector_img{
        height: 35px;
    }
    
    :root{
        --flag-w: 40px;
    }
    
    .dropdown_wrapper{
        bottom: -130px;
        padding-top: 130px;
    }
}